import { createWebHistory, createRouter } from "vue-router";
import MainHome from './components/MainHome.vue';
import TextList from './components/TextList.vue';
import TextDetail from './components/TextDetail.vue';
import TextAuthor from './components/TextAuthor.vue';
import TextComment from './components/TextComment.vue';
// import ErrorPage404 from './components/ErrorPage404.vue';

const routes = [
  {
    path: "/",
    component : MainHome,
  },{
    path: "/list",
    component: TextList
  },{
    path: "/detail/:id(\\d+)",
    component: TextDetail,
    children : [
      {
        path: "author",
        component : TextAuthor.vue
      },
      {
        path: "comment",
        component : TextComment.vue
      }
    ]
  }
  // ,{
  //   path: "/:anything(.*)",
  //   component: ErrorPage404,
  // }, 
];

const router = createRouter({
  history: createWebHistory(),
  routes,
});

export default router; 